<template>
  <user>
    <html-head 
      title="Products"
    ></html-head>

    <page-header>
      <template v-slot:title>All Products</template>
    </page-header>

    <template v-if="resources">
      <crud-table-card 
        :api="api" 
        :headers="headers" 
        :optionsChanged="optionsChanged" 
        :resources="resources" 
        name="product"
        :view="true"
      >
        <template v-slot:item.actions="{ item }">
            <option-buttons
              :api="api"
              :item="item"
              name="product"
              :view="true"
            >
              <template v-slot:append>
                <allowed :code="`inventories.update`">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn 
                        icon
                        v-bind="attrs"
                        v-on="on"
                        :to="{ name: 'inventories.edit', params: { id: item._id } }"
                        exact
                      >
                        <v-icon>mdi-folder-edit-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>View Stock</span>
                  </v-tooltip>
                </allowed>
              </template>
            </option-buttons>
        </template>
      </crud-table-card>
    </template>
  </user>
</template>

<script>
import Api from './routes/api'
import Resources from '@/core/models/Resources'

export default {
  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Code',
        align: 'start',
        value: 'code',
      },
      {
        text: 'Image',
        align: 'start',
        value: 'image',
      },
      {
        text: 'Name',
        align: 'start',
        value: 'name',
      },
      {
        text: 'Variations',
        align: 'start',
        value: 'variations.length',
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],

    resources: new Resources
  }),

  methods: {
    optionsChanged ( options ) {
      this.resources.optionsChanged(
        {
          method: 'get',
          url: Api.main(),
          params: {
            search: this.resources.search,
            searchFields: "code,name",
            options: this.resources.options
          }
        }, 
        options 
      )
    },
  },

  mounted () {
    this.resources.load( false )
  }
}
</script>